.post {
  display: flex;
  background: white;
}

.post-votes {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  background: #f8f9fa;
  border-radius: 4px 0 0 4px;
  min-width: 40px;
}

.vote-btn {
  background: transparent;
  border: none;
  padding: 4px;
  font-size: 12px;
  color: #878a8c;
  cursor: pointer;
  border-radius: 2px;
}

.vote-btn:hover:not(:disabled) {
  background: #edeff1;
}

.vote-btn:disabled {
  cursor: default;
  opacity: 0.5;
}

.vote-btn.upvote.active {
  color: #ff4500;
}

.vote-btn.downvote.active {
  color: #7193ff;
}

.vote-count {
  font-size: 12px;
  font-weight: 700;
  padding: 4px 0;
}

.post-content {
  flex: 1;
  padding: 8px 8px 8px 0;
  min-width: 0;
}

.post-meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 4px;
  font-size: 12px;
  color: #787c7e;
  margin-bottom: 8px;
}

.post-community {
  font-weight: 700;
  color: #1c1c1c;
}

.post-community:hover {
  text-decoration: underline;
}

.post-author a:hover {
  text-decoration: underline;
}

.separator {
  color: #787c7e;
}

.post-title-link {
  display: block;
}

.post-title {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.3;
  margin-bottom: 8px;
  color: #222;
}

.post-title-link:hover .post-title {
  color: #0079d3;
}

.post-body {
  font-size: 14px;
  color: #1c1c1c;
  margin-bottom: 8px;
  max-height: 250px;
  overflow: hidden;
  position: relative;
}

.post-body::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: linear-gradient(transparent, white);
}

.post-actions {
  display: flex;
  gap: 4px;
}

.post-action {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 8px 12px;
  background: transparent;
  border: none;
  font-size: 12px;
  font-weight: 700;
  color: #878a8c;
  border-radius: 2px;
  cursor: pointer;
}

.post-action:hover {
  background: #edeff1;
}

.post-action.active {
  color: #0079d3;
}

.share-tooltip {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: #1c1c1c;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 10px;
  margin-bottom: 4px;
  white-space: nowrap;
}

.post-community-link {
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 700;
  color: #1c1c1c;
}

.post-community-icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  object-fit: cover;
}

.post-community-icon-placeholder {
  font-size: 10px;
  background: #edeff1;
  padding: 2px;
  border-radius: 50%;
}

.author-link {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  margin-left: 4px;
}

.post-author-avatar {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  object-fit: cover;
}

.delete-btn:hover {
  background: #ffebee;
  color: #d32f2f;
}
